<template>
  <div>
    <v-row>
      <v-col md="12" cols="12">
        <BlockItem>
          <span slot="title">مدیریت دسته بندی ها</span>
          <template slot="icon">mdi-comment</template>

          <v-form ref="form" >
            <v-row v-if="renderComponent" >
              <v-col
                  cols="12"
                  md="3"
              >
                <v-text-field
                    outlined
                    :rules="[$store.state.rules.required(title,'عنوان')]"
                    v-model="title"
                    label="عنوان"
                    dense
                    color="txtf"
                >

                </v-text-field>
              </v-col>
              <v-col
                  cols="12"
                  md="3"
              >
                <v-autocomplete
                    outlined
                    :items="data"
                    item-text="title"
                    item-value="id"
                    v-model="parent"
                    label="والد"
                    dense
                    color="txtf"
                    item-color="txtf"
                    clearable
                >

                </v-autocomplete>
              </v-col>
              <v-col
                  cols="12"
                  md="3"
              >
                <v-text-field
                    outlined
                    :rules="[$store.state.rules.required(name,'لاتین')]"
                    v-model="name"
                    label="لاتین"
                    color="txtf"
                    dense
                >

                </v-text-field>
              </v-col>
              <v-col
                  cols="12"
                  md="1"
              >
                <v-checkbox
                    outlined
                    v-model="enabled"
                    color="info"
                    label="فعال"
                    dense
                >

                </v-checkbox>
              </v-col>
              <v-col
                  cols="12"
                  md="2"
              >
                <v-btn v-if="!id && $store.state.permissions && $store.state.permissions['create-category']" dark class="dash-btns dash-btns--blue" :disabled="loading" @click="addCategory()">
                  + افزودن
                </v-btn>
<!--                <v-btn v-else dark color="orange darken-2" :disabled="loading" @click="editCategory()">
                  ویرایش
                </v-btn>-->
                <v-btn text dark v-if="id" @click="editCategory()" color="#17a2b8" >
                  <v-icon  color="#17a2b8" :disabled="loading"  >mdi-check</v-icon>
                  ویرایش
                </v-btn>
                <v-btn small height="36" text dark v-if="id" @click="cleanEdit()" color="red" >
                <v-icon  color="error" :disabled="loading"  >mdi-close</v-icon>
                </v-btn>
              </v-col>
            </v-row>
          </v-form>

          <v-data-table
              hide-default-footer
              :headers="headers" :loading="loading" :items="data" item-key="id" disable-sort
          >

            <template v-slot:[`item.enabled`]="{item}">
              <span class="table-flag-enabled table-flag-enabled--green" v-if="item.enabled" >فعال</span>
              <span class="table-flag-enabled table-flag-enabled--red" v-else>غیر فعال</span>
            </template>

            <template v-slot:[`item.func`]="{item}">
              <div class="d-flex justify-center">
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['change-category']" @clickBtn="toEdit(item)" color="#17a2b8" icon="mdi-pencil" alt="ویرایش کردن" ></a-sbtn>
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['delete-category']" @clickBtn="toDelete(item.id,'/blog/api/v1/category/delete-category/')" color="#DC3545" icon="mdi-delete" alt="حذف کردن" ></a-sbtn>
              </div>
            </template>


          </v-data-table>
        </BlockItem>
      </v-col>
    </v-row>
    <DeleteDialog :ddata="deleteData" @deleteFn="fetchCategories()"></DeleteDialog>
  </div>
</template>

<script>
import axios from "axios";
import BlockItem from "@/components/Misc/BlockItem";
import DeleteDialog from "@/components/Misc/DeleteDialog";
import {forceRerender} from "@/components/Misc/forceRerender";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";

export default {
  name: "DashBlogCategory",
  mixins : [forceRerender],
  components: {
    ASbtn,
    DeleteDialog,
    BlockItem
  },
  created() {
    this.checkQueries();
    this.fetchCategories();
  },
  data() {
    return {

      id: null,
      enabled: true,
      name: null,
      parent: null,
      title: null,
      options: {},
      loading: false,
      data: [],
      deleteData: {
        api: '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      commentData: {
        dialog: false,
        loading: false,
        idSelected: null,
      }
    }
  },
  computed: {
    headers() {
      return [
        {
          text: 'شماره',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'id',
        },
        {
          text: 'عنوان',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'title',
        },
        {
          text: 'فعال', class: 'header-table-gray',
          align: 'center',
          value: 'enabled',
        }, {
          text: 'نامک', class: 'header-table-gray',
          align: 'center',
          value: 'cat_slug',
        },
        {
          text: 'لاتین', class: 'header-table-gray',
          align: 'center',
          value: 'name',
        },
        {
          text: 'والد', class: 'header-table-gray',
          align: 'center',
          value: 'parent',
        },
        {
          text: 'پست ها', class: 'header-table-gray',
          align: 'center',
          value: 'post_count',
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'func',
        },


      ]
    },
  },
  methods: {
    addCategory() {

      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        const data = {
          enabled : this.enabled,
          name : this.name,
          parent : this.parent,
          title : this.title
        }
        this.enabled = true
        this.id= null
        this.name = null
        this.parent = null
        this.title = null
        this.loading = true;
        this.forceRerender();
        axios.post(this.$store.state.host + '/blog/api/v1/category/create-category', data, {

          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },

        }).then(() => {
          this.notify('دسته بندی افزوده شد', 'info');
          this.fetchCategories();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    editCategory() {

      this.$refs.form.validate();
      if (this.$refs.form.validate()) {
        const data = {
          id : this.id,
          enabled : this.enabled,
          name : this.name,
          parent : this.parent,
          title : this.title
        }
        this.enabled = true
        this.id= null
        this.name = null
        this.parent = null
        this.title = null
        this.loading = true;
        this.forceRerender();
        axios.patch(this.$store.state.host + '/blog/api/v1/category/change-category/'+data.id, data, {

          headers: {
            Accept: "application/json",
            Authorization: `Bearer ${this.$store.state.userId}`,
          },

        }).then(() => {
          this.notify('دسته بندی ویرایش شد', 'warning');
          this.fetchCategories();
        }).catch(error => {
          this.errorHandle(error.response)
        })
      }
    },
    toEdit(item){
      this.id = item.id;
      this.title = item.title;
      this.parent = item.parent;
      this.enabled = item.enabled;
      this.name = item.name;
      this.title = item.title;
    },
    cleanEdit(){
      this.id = null;
      this.title = null;
      this.parent = null;
      this.enabled = true;
      this.name = null;
      this.title = null;
      this.forceRerender();
    },
    toDelete(id, api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },
    checkQueries() {
      if (this.$route.query.page) {
        this.options.page = parseInt(this.$route.query.page);
      }

      if (this.$route.params.id) {
        this.options.category = this.$route.params.id;
      }

      if (this.$route.query.search) {
        this.options.search = this.$route.query.search;
      }

      if (this.$route.query.ordering) {
        this.options.ordering = this.$route.query.ordering;
      }

      if (this.$route.query.tag) {
        this.options.tag = this.$route.query.tag;
      }
    },

    fetchCategories() {
      this.loading = true;
      axios.get(this.$store.state.host + '/blog/api/v1/category/categories', {
        params: this.options
      }).then(response => {
        this.data = response.data.data;
        this.loading = false;
      }).catch(error => {
        this.errorHandle(error.response)
      })
    }
  }
}
</script>

